* {
    box-sizing: border-box;
    margin: 0%;
    padding: 0%;
    font-family: "Roboto", sans-serif;
    scroll-behavior: smooth;
}

.AuthWrapper {
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
}

.TextWrapper {
    margin-bottom: 5rem;
    width: 40%;
    line-height: 1.5;
}

.TextWrapper h1 {
    line-height: 1;
    font-size: 2.5rem;
}

.TextWrapper p {
    margin-top: 3%;
    width: 80%;
}

.Auth {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.AuthPanel {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.authForm {
    width: 30vw;
    min-height: 70vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

ul {
    width: 100%;
    flex-grow: 1;
}

.authForm>input,
.inputContainer input,
.inputContainer select {
    height: 2.9rem;
    width: 100%;
    border: 1px solid hsla(0, 0%, 0%, 10%);
    border-radius: 7px;
    padding: 0 3%;
    margin: 2% 0;
    background-color: #fafafa;
}

.authForm>input:focus,
.page>input:focus {
    outline: none;
}

.inputlabel {
    align-self: flex-start;
}

.AuthWrapper input[type="submit"],
.AuthWrapper input[type="button"] {
    background-color: #f5d042;
    border: none;
    font-size: 1.3rem;
    font-weight: 500;
    height: 3rem;
    width: 90%;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.option {
    display: flex;
    width: 90%;
    margin: 3% 0;
    justify-content: space-between;
}

.option>div {
    display: flex;
    align-items: center;
    gap: 3px;
}

.option {
    font-size: clamp(0.5vw, 20px, 1rem);
}

.pages {
    width: 100%;
    min-height: 70vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.page {
    height: 100%;
    width: 100%;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.page.show {
    display: flex;
}

.AuthWrapper input[type="submit"]:hover,
.AuthWrapper input[type="button"]:hover {
    box-shadow: 0px 0px 20px hsl(48, 78%, 73%, 0.8);
}

.RsgesterOption {
    width: 100%;
    gap: 10%;
    margin-top: 1rem;
    padding: 0 2%;
    display: flex;
    justify-content: space-between;
}

.AuthWrapper .RsgesterOption input {
    height: 2.3rem;
    width: 40%;
    font-size: 1rem;
    border-radius: 0.31rem;
}

.AuthWrapper .RsgesterOption input:first-child {
    background: none;
    border: 1px solid hsl(0, 0%, 0%, 0.6);
}

.AuthWrapper .RsgesterOption input:first-child:hover {
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.8);
}

.switcher {
    height: 12vh;
}

.switcher ul {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5%;
}

.switer_li {
    height: 60%;
    width: 11rem;
    display: grid;
    place-content: center;
    border-radius: 20px;
    cursor: pointer;
    z-index: 1;
}

.switer_li.active {
    background-color: #f5d042;
}

@media screen and (max-width: 700px) {
    .AuthWrapper {
        flex-direction: column-reverse;
    }
    .authForm {
        width: 80vw;
    }
    .TextWrapper {
        width: 100%;
        margin-top: 6vh;
        line-height: 1.5;
    }
    .TextWrapper h1 {
        line-height: 1;
        font-size: 2rem;
    }
    .TextWrapper p {
        margin-top: 8%;
        width: 100%;
    }
}